<template>
  <div>
    <!-- 顶部标签 -->
    <van-nav-bar title="商品详情" left-arrow class="bgc">
      <van-icon name="arrow-left" slot="left" @click="Return()" />
      <van-icon
        @click="goShopcart"
        name="cart-o"
        slot="right"
        :badge="shopcartRows == 0 ? '' : shopcartRows"
      />
    </van-nav-bar>
    <div class="content">
      <div class="detail-box">
        <van-image :src="details.large_img" />

        <ul class="list">
          <li class="item">
            <h2 class="name">
              <div class="name">{{details.name}}</div>
              <div class="enname">{{details.enname}}</div>
            </h2>
            <h2 class="enname"></h2>
            <h2 class="price">￥{{details.price}}</h2>
          </li>
        </ul>

        <!-- 商品规格 -->
        <div class="rule-box">
          <div class="taste-box" v-for="(Item,index) in tastes" :key="index">
            <span class="title">{{Item.title}}</span>
            <span
              class="taste"
              :class="{'active':tastitem.isActive}"
              v-for="(tastitem,index) in Item.tag"
              :key="index"
              @click="toggleRuleTag(Item, tastitem)"
            >{{tastitem.name}}</span>
          </div>
          <div class="step">
            <span class="num">数量</span>
            <van-stepper default-value="1" v-model="count" input-width="40px" button-size="32px" />
          </div>
        </div>

        <!-- 商品描述 -->
        <div class="introduce">
          <h3>商品描述</h3>
          <p v-for="(item,index) in details.desc" :key="index">{{item}}</p>
        </div>
      </div>
    </div>

    <van-goods-action>
      <van-goods-action-icon
        @click="likeAndNotLike"
        :icon="isLike?'star':'star-o'"
        :text="like"
        color="#130101"
      />
      <van-goods-action-button color="#690505" text="加入购物车" @click="addShopcart" />
      <van-goods-action-button color="#2C1907" text="立即购买" @click="goShopcart" />
    </van-goods-action>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pid: "",

      //商品详情数据
      details: [],

      // 口味
      tastes: [],

      isLike: false,
      like: "",

      // 商品数量
      count: 1,

      //购物车条数，选购商品种类
      shopcartRows: 0
    };
  },

  created() {
    // 获取pid值
    this.pid = this.$route.query.pid;

    // 获取数据
    this.getDetailData();

    // 查询是否收藏
    this.findLike(this.pid);

    // 查询商品条数
    this.findShopcart();
  },

  methods: {
    getDetailData() {
      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid
          // rule:
        }
      })
        .then(res => {
          this.$toast.clear();
          let protastes = ["tem", "sugar", "milk", "cream"];

          if (res.data.code == 600) {
            this.details = res.data.result[0];
            

            // 商品描述数据拆分
            this.details.desc = this.details.desc.split(/\n/);
            // 口味
            protastes.forEach(v => {
              if (this.details[v] != "") {
                let protaste = {
                  title: this.details[v + "_desc"],
                  tag: []
                };
                let tag = this.details[v].split("/");
                // 规格确认
                tag.forEach((item, i) => {
                  let t = {
                    name: item,
                    isActive: i == 0
                  };
                  protaste.tag.push(t);
                });

                this.tastes.push(protaste);
              }
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 商品规格选择
    toggleRuleTag(item, v) {
      if (v.isActive) {
        return;
      }

      //查找已经激活的规格标签
      for (let i = 0; i < item.tag.length; i++) {
        if (item.tag[i].isActive) {
          item.tag[i].isActive = false;
          break;
        }
      }

      v.isActive = true;
    },

    // 查询收藏商品接口
    findLike(pid) {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 1000) {
            if (res.data.result.length > 0) {
              this.isLike = true;
              this.like = "已收藏";
            } else {
              this.isLike = false;
              this.like = "未收藏";
            }
          } else {
            //如果token无效，则跳转到登录页面
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //收藏-取消收藏商品
    likeAndNotLike() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");
      let url = this.isLike ? "/notlike" : "/like";

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 800 || res.data.code == 900) {
            this.isLike = !this.isLike;
            this.like = res.data.msg;
            this.$toast(this.like);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //查询购物车商品条数
    findShopcart() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == 5000) {
            this.shopcartRows = res.data.result.length;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 跳转购物车页面
    goShopcart() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      // 获取商品规格
      let rules = this.tastes;
      let rule = [];

      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break;
          }
        }
      });

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          pid: this.pid,
          count: this.count,
          rule: rule.join("/"),
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 3000) {
            // 修改数据数量
            
            if (res.data.status == 1) {
              this.shopcartRows++;
            }

            this.$toast(res.data.msg);
            this.$router.push({ name: "Shopcart" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 添加购物车商品接口
    addShopcart() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      // 获取商品规格
      let rules = this.tastes;
      let rule = [];

      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break;
          }
        }
      });

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          pid: this.pid,
          count: this.count,
          rule: rule.join("/"),
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 3000) {
            // 修改数据数量
            
            if (res.data.status == 1) {
              this.shopcartRows++;
            }

            this.$toast(res.data.msg);
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 返回上一页面
    Return() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/css/commaint.less";
// 公共样式
.bgc {
  background-color: #2c1907;
}
.van-icon {
  color: #fff;
  font-size: 18px;
}
/deep/ .van-nav-bar__title {
  color: #fff;
}

.rule-box {
  margin: 10px auto;
  padding: 5px;
  span {
    display: inline-block;
    width: 60px;
    font-size: 14px;
  }
}

.taste-box {
  height: 25px;

  .title {
    line-height: 20px;
    width: 50px;
  }
  .taste {
    width: 60px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    border: 1px solid #000;
    border-radius: 50px;
    margin: 0 5px;
  }
}
.list {
  padding: 5px;
  .item {
    height: 50px;
    line-height: 50px;
    position: relative;
  }
  .price {
    right: 0;
  }
  h2 {
    display: inline-block;
    margin: 0;
    position: absolute;
  }
  .name,
  .enname {
    height: 20px;
  }
}
.introduce {
  padding: 5px;
  margin-bottom: 30px;

  p {
    color: rgb(47, 31, 31);
    line-height: 20px;
  }
}
.van-button {
  height: 30px;
  margin: 0 5px;
  padding: 1;
  border: 1px solid #e7e7e7;
}

.step {
  margin-top: 25px;
  font-size: 24px;
  position: relative;

  .van-stepper {
    right: 0;
    position: absolute;
  }
  .num {
    position: absolute;
  }
}

.active {
  background-color: #130101;
  color: #fff;
}
</style>